
.fast{
    -webkit-animation-duration: 0.3s;
    animation-duration: 0.3s;
    -webkit-animation-fill-mode:both;
    animation-fill-mode:both
}

#themecolors .selector, .cursor{
    cursor: pointer;
}
#google-btn{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.img-avatar{
    width:200px;
    height: 200px;
}

.w100{
    width: 100px;
}
.w140{
    width: 140px;
}
.w200{
    width: 200px;
}
.avatar{
    border-radius: 100%;
    width: 30px;
}
.oculto {
    display: none;
}

.fondo-modal-imagen{
    background-color: rgba(0, 0, 0, 0.4);
    height: 100%;
    left: 0px;
    position: fixed;
    top: 0px;
    width: 100%;
    z-index: 999;
}